//Layout
$mobile_maxWidth: 375px;
$desktop_maxWidth: 1440px;

//Colors
$color_primary_Cyan: hsl(179, 62%, 43%);
$color_primary_lighterCyan: hsl(179, 62%, 46%);
$color_primary_brightYellow: hsl(71, 73%, 54%);
$color_neutral_lightGray: hsl(204, 43%, 93%);
$color_neutral_grayishBlue: hsl(218, 22%, 67%);

//Typography
@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap');
//Fonts
$mainFont: 'Karla', sans-serif;

@mixin mainFont_regular {
    font-family: $mainFont;
    font-weight: 400;
}

@mixin mainFont_bold {
    font-family: $mainFont;
    font-weight: 700;
}

.container {
    max-width: $desktop_maxWidth;
    border-radius: .5rem;
    margin-inline: 1rem;
    display: grid;
    grid-template-rows: 1fr 1.5fr;
    grid-template-columns: 1fr 1fr;
    box-shadow: 0 0 1rem $color_neutral_grayishBlue;

    .title {
        grid-row: 1 / 2;
        grid-column: 1 / 3;
        background-color: white;
        border-top-left-radius: .5rem;
        border-top-right-radius: .5rem;
        padding-inline: 1rem;
        padding: 2rem;

        h1 {
            align-self: start;
            color: $color_primary_Cyan;
            font-size: 1.5rem;
            padding-bottom: 1rem;
        }

        h2 {
            align-self: center;
            color: $color_primary_brightYellow;
            font-size: 1.2rem;
            padding-bottom: 1rem;


        }

        p {
            align-self: end;
            color: $color_neutral_grayishBlue;
            padding-bottom: 1rem;
            line-height: 1.6;

        }
    }

    .cta {
        align-self: stretch;
        justify-self: stretch;
        background-color: $color_primary_Cyan;
        grid-row: 2 / 3;
        grid-column-start: 1 / 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        border-bottom-left-radius: .5rem;
        padding-inline: 1.5rem;
        color: $color_neutral_lightGray;

        h2 {
            font-size: 1.5rem;
        }

        h3 {
            align-self: flex-start;
            font-size: 2rem;
            color: white;

            span {
                padding-left: 1rem;
                @include mainFont_regular();
                color: $color_neutral_lightGray;
                font-size: 1.3rem;
            }
        }

        button {
            @include mainFont_bold();
            font-size: 1.1rem;
            background-color: $color_primary_brightYellow;
            color: $color_neutral_lightGray;
            width: 100%;
            height: 15%;
            align-self: center;
            border: none;
            border-radius: .5rem;
            box-shadow: 0 0 1rem gray;

            &:hover {
                cursor: pointer;
            }

            &:active {
                background-color: $color_primary_lighterCyan;
            }

        }
    }

    .whyUs {
        align-self: stretch;
        justify-self: stretch;
        background-color: $color_primary_lighterCyan;
        grid-row: 2 / 3;
        grid-column: 2 / 3;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-bottom-right-radius: .5rem;
        padding-inline: 1.5rem;


        .listTitle {
            @include mainFont_bold();
            padding-bottom: 1.5rem;
            font-size: 1.5rem;
            color: white;
        }

        ul {
            list-style: none;
            @include mainFont_regular();

            li {
                color: $color_neutral_lightGray;
            }

        }
    }
}

@media only screen and (max-width: 390px) {
    .container {
        width: 100%;
        max-width: $mobile_maxWidth;
        height: 100vh;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: 1fr;
        margin-inline: 0;
        overflow: auto;
        border-radius: 0;

        .title {
            grid-row: 1 / 2;
            grid-column: 1 / 2;
            border-radius: 0;
        }

        .cta {
            grid-row: 2 / 3;
            grid-column: 1 / 2;
            border-radius: 0;
            min-height: 250px;

            h2 {
                align-self: start;
            }

            h3 {
                align-self: start;
                font-size: 2rem;
                color: white;

                span {
                    @include mainFont_regular();
                    color: $color_neutral_lightGray;
                    font-size: 1.3rem;
                }
            }

            button {
                height: 20%;
            }

            p {
                align-self: start;
            }

        }

        .whyUs {
            grid-row: 3 / 4;
            grid-column: 1 / 2;
            border-radius: 0;
            align-items: flex-start;
            padding-block: 1.5rem;
        }
    }
}