@import url("https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap");
.container {
  max-width: 1440px;
  border-radius: 0.5rem;
  margin-inline: 1rem;
  display: grid;
  grid-template-rows: 1fr 1.5fr;
  grid-template-columns: 1fr 1fr;
  box-shadow: 0 0 1rem hsl(218deg, 22%, 67%);
}
.container .title {
  grid-row: 1/2;
  grid-column: 1/3;
  background-color: white;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  padding-inline: 1rem;
  padding: 2rem;
}
.container .title h1 {
  align-self: start;
  color: hsl(179deg, 62%, 43%);
  font-size: 1.5rem;
  padding-bottom: 1rem;
}
.container .title h2 {
  align-self: center;
  color: hsl(71deg, 73%, 54%);
  font-size: 1.2rem;
  padding-bottom: 1rem;
}
.container .title p {
  align-self: end;
  color: hsl(218deg, 22%, 67%);
  padding-bottom: 1rem;
  line-height: 1.6;
}
.container .cta {
  align-self: stretch;
  justify-self: stretch;
  background-color: hsl(179deg, 62%, 43%);
  grid-row: 2/3;
  grid-column-start: 1/2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-bottom-left-radius: 0.5rem;
  padding-inline: 1.5rem;
  color: hsl(204deg, 43%, 93%);
}
.container .cta h2 {
  font-size: 1.5rem;
}
.container .cta h3 {
  align-self: flex-start;
  font-size: 2rem;
  color: white;
}
.container .cta h3 span {
  padding-left: 1rem;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  color: hsl(204deg, 43%, 93%);
  font-size: 1.3rem;
}
.container .cta button {
  font-family: "Karla", sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
  background-color: hsl(71deg, 73%, 54%);
  color: hsl(204deg, 43%, 93%);
  width: 100%;
  height: 15%;
  align-self: center;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 0 1rem gray;
}
.container .cta button:hover {
  cursor: pointer;
}
.container .cta button:active {
  background-color: hsl(179deg, 62%, 46%);
}
.container .whyUs {
  align-self: stretch;
  justify-self: stretch;
  background-color: hsl(179deg, 62%, 46%);
  grid-row: 2/3;
  grid-column: 2/3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom-right-radius: 0.5rem;
  padding-inline: 1.5rem;
}
.container .whyUs .listTitle {
  font-family: "Karla", sans-serif;
  font-weight: 700;
  padding-bottom: 1.5rem;
  font-size: 1.5rem;
  color: white;
}
.container .whyUs ul {
  list-style: none;
  font-family: "Karla", sans-serif;
  font-weight: 400;
}
.container .whyUs ul li {
  color: hsl(204deg, 43%, 93%);
}

@media only screen and (max-width: 390px) {
  .container {
    width: 100%;
    max-width: 375px;
    height: 100vh;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 1fr;
    margin-inline: 0;
    overflow: auto;
    border-radius: 0;
  }
  .container .title {
    grid-row: 1/2;
    grid-column: 1/2;
    border-radius: 0;
  }
  .container .cta {
    grid-row: 2/3;
    grid-column: 1/2;
    border-radius: 0;
    min-height: 250px;
  }
  .container .cta h2 {
    align-self: start;
  }
  .container .cta h3 {
    align-self: start;
    font-size: 2rem;
    color: white;
  }
  .container .cta h3 span {
    font-family: "Karla", sans-serif;
    font-weight: 400;
    color: hsl(204deg, 43%, 93%);
    font-size: 1.3rem;
  }
  .container .cta button {
    height: 20%;
  }
  .container .cta p {
    align-self: start;
  }
  .container .whyUs {
    grid-row: 3/4;
    grid-column: 1/2;
    border-radius: 0;
    align-items: flex-start;
    padding-block: 1.5rem;
  }
}